.myOrdersPage {
  width: 100vw;
  max-width: 100%;
  padding: 0 7vmax;
  box-sizing: border-box;
  background-color: rgb(235, 235, 235);
  position : static;
  position: fixed; 
   top: 10%;
  left: 0;
  height: 90vh;  display: flex;
  flex-direction: column;
}

.backbutton{
  font-size: 40px;
  color: black;
  border: solid 1px black;
  /* padding: 5px 15px; */
}
#myOrdersHeading {
  text-align: center;
  font: 400 1.2vmax "Roboto";
  padding: 0.5vmax;
  
  box-sizing: border-box;
  color: rgb(255, 255, 255);
  transition: all 0.5s;
  background-color: rgb(44, 44, 44);
}

.myOrdersTable {
  
  background-color: white;
}
.MuiDataGrid-root .MuiDataGrid-main {
  display: flex;
  position:static;
  flex-grow: 1;
  flex-direction: column;
}

.myOrdersTable div {
  font: 300 1vmax "Roboto";
  color: rgba(0, 0, 0, 0.678);
  border: none;
}

.myOrdersTable a {
  color: rgba(0, 0, 0, 0.527);
  transition: all 0.5s;
}

.myOrdersTable a:hover {
  color: tomato;
}

.MuiDataGrid-columnHeader {
  position : static;
  background-color: #1b2430;
  padding: 1vmax !important;
}

.MuiDataGrid-columnHeader div {
  color: rgb(255, 255, 255);
  font: 500 1.1vmax "Roboto" !important;
}

.MuiDataGrid-iconSeparator {
  display: none !important;
}

@media screen and (max-width: 600px) {
  .myOrdersPage {
    padding: 0;
    /* position: static; */
    width: 100vw;
    
  }

  #myOrdersHeading {
    font: 20 2.2vmax "Roboto";
    padding: 0.2vw;
  }

  .myOrdersTable div {
    font: 20 4vw "Roboto";
  }

  .MuiDataGrid-columnHeader {
    padding: 0.4px !important;
  }

  .MuiDataGrid-columnHeader div {
    font: 500 5vw "Roboto" !important;
  }
}
