nav {

  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  background: #1b2430;
  padding: 10px 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
}

.hide{
  display: none;
}
.nothide{
  display: block;
}

nav img{
  width: 60px;
}

.menu {
  display: flex;
  width: 90%;
  justify-content: center;
  align-items: center;
}

#navbar {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

#navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}

#navbar li a {
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  transition: 0.3s ease-in-out;
}

#navbar li a:hover,
#navbar li a.active {
  color: #17cf97;
}

#navbar li a:hover::after,
#navbar li a.active::after {
  content: "";
  width: 50%;
  height: 2px;
  background: #17cf97;
  position: absolute;
  bottom: -4px;
  left: 20px;
}

.mobile-bar i {
  display: inline;
  font-size: 35px;
  color: #fff;
  align-items: center;
  padding-right: 10px;
  margin: 0px 55px 0px 30px;
}

#mobile {
  display: none;
}

#cart i {
  margin: 0px 55px 0px 30px;
}

#Search i {
  font-size: 24px;
  color: #fff;
  cursor: pointer;
  margin: 0px 30px 13px 0px;
}

#mobile i,
#cartbox i {
  font-size: 24px;
  color: #fff;
  cursor: pointer;
  margin: 0px 30px 13px 0px;
}



div #accountImage {
  width: 50px;
  border-radius: 40%;
}

@media screen and (max-width: 769px) {

  
#notmobile{
  display: none;
}

  #navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 0px;
    opacity: 1;
    right: -305px;
    width: 300px;
    height: 100vh;
    /* background: #2a3239; */
    background: black;
    /* box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1); */
    padding: 40px 0 0 10px;
    transition: 0.3s ease-in-out;
  }


  div #accountImage {
    display: none;
    width: 50px;
    border-radius: 40%;
  }

  #navbar.active {
    right: 0px;
  }

  #navbar li {
    margin-bottom: 25px;
  }

  #mobile {
    display: block;
    margin-right: 5px;
  }

  #Search i {
    font-size: 24px;
    color: #fff;
    cursor: pointer;
    margin: 0px 0px 0px 25px;
  }

  #login i {
    font-size: 24px;
    color: #fff;
    cursor: pointer;
    margin: 0px 0px 0px 25px;
  }

  #mobile i,
  #cartbox i {
    font-size: 24px;
    color: #fff;
    cursor: pointer;
    margin: 0px 0px 0px 15px;
  }

  .back-arrow {
    display: inline;
    font-size: 28px;
    /* color: #fff; */
    align-items: center;

    /* margin: 0px 55px 0px 30px ; */

  }

  .mobile-bar i {
    display: inline;
    font-size: 28px;
    color: #fff;
    align-items: center;
    padding-right: 10px;
    margin: 0px 20px 0px 12px;
  }

}